<template>
  <div class="edit-article-dialog">
    <el-dialog customClass="dialog-wrap" :title="dialogTitle" :visible.sync="dialogFormVisible" @close="hide" width="680px" top="5vh" :center="true">
      <el-form :model="articleData" ref="form" :rules="formValidate" label-position="left" label-width="100px" v-loading="loading">
        <el-row :gutter="20">
          <el-col :span="23">
            <span class="title-tip">注: 最多40个字</span>
            <el-form-item label="标题:" prop="title">
              <el-input v-model="articleData.title" auto-complete="off" placeholder="请输入资讯标题"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="18">
            <el-form-item label="标签:" prop="labels">
              <el-select v-model="articleData.labels" filterable multiple allow-create
                remote :remote-method="debounceFunc(queryLabels, 300, false)"
                placeholder="请选择标签" style="width: 100%">
                <el-option v-for="(item, index) in articleLabels" :key="index" :label="item.labelName" :value="item.labelName">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="5" v-if="dialogTitle === '编辑资讯'">
            <el-button type="primary" @click="getLabels" class="get-label-btn">获取分词结果</el-button>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="11">
            <el-form-item label="作者:" inline="true">
              <el-input v-model="articleData.author" auto-complete="off" placeholder="请输入文章作者"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item prop="directoryId" label="所属栏目:">
              <el-select style="width:100%" v-model="articleData.directoryId" placeholder="请选择所属栏目">
                <el-option v-for="item in columnListData" :key="item.id" :label="item.directoryName" :value="item.id">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="11">
            <el-form-item prop="infoAccountId" label="来源:">
              <el-select style="width:100%" v-model="articleData.infoAccountId" :filterable="true" placeholder="请选择所属栏目">
                <el-option v-for="(item, index) in infoAccount" :key="index" :label="item.nickName" :value="item.infoAccountId">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="发布时间:">
              <el-date-picker v-model="articleData.publishTime" type="datetime" placeholder="请选择发布时间" @change="formatPublishTime">
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="11">
            <el-form-item label="资讯时效性:">
              <el-radio v-model="articleData.isTimeliness" label="N">否</el-radio>
              <el-radio v-model="articleData.isTimeliness" label="Y">是</el-radio>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="articleData.isTimeliness==='Y'">
            <el-form-item label="有效截止时间:">
              <el-date-picker v-model="articleData.validityDate" type="datetime" placeholder="请选择截止时间" value-format="yyyy-MM-dd HH:mm:ss">
              </el-date-picker>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="11">
            <el-form-item label="展示模板:">
              <el-select style="width:100%" v-model="articleData.extraInfo" placeholder="请选择模板类型">
                <el-option v-for="item in templateType" :key="item.value" :label="item.label" :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="23">
            <el-form-item label="封面:">
              <el-upload :auto-upload="true" :on-change="changeImage" :action="ajaxUrl.baseUrl + ajaxUrl.uploadArticleImage" :file-list="articleData.imageList" :before-upload="beforeCoverUpload" ref="upload" :on-success="handleCoverSuccess" :limit="5" list-type="picture-card" :on-preview="handlePictureCardPreview" :on-remove="handleRemove">
                <i class="el-icon-plus"></i>
                <div slot="tip" class="upload-tip">建议宽高比200:120</div>
              </el-upload>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row :gutter="20">
					<el-col :span="23">
						 <el-form-item label="摘要:">
							<el-input type="textarea" v-model="articleData.summary" auto-complete="off" :maxlength="40" placeholder="请输入摘要"></el-input>
						 </el-form-item>
					</el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="23">
            <el-form-item label="正文:" v-if="articleData.articleType === '00' || articleData.articleType === '02' || !articleData.articleType">
              <za-editor ref="editor" id="editor_id" height="500px" width="500px"
                :minWidth="500" :resizeType="1" urlType="absolut"
                :content="articleData.content" :loadStyleMode="false"
                @on-content-change="onContentChange">
              </za-editor>
            </el-form-item>
            <!-- 视频 -->
            <div v-else style="margin-bottom: 30px">
              <el-form-item label="视频标题:">
                <el-input v-model="articleData.content.title" placeholder="请输入title"></el-input>
              </el-form-item>
              <el-table :data="articleData.content.content" border>
                <el-table-column type="expand">
                  <template slot-scope="scopeRoot">
                    <el-form label-position="left">
                      <el-form-item label="文本:">
                        <el-input v-model="scopeRoot.row.text" style="width: 80%;margin-left: 8px"></el-input>
                      </el-form-item>

                      <el-table :data="scopeRoot.row.videos">
                        <el-table-column type="index" label="排序" width="50"></el-table-column>
                        <el-table-column label="视频链接" prop="src">
                          <template slot-scope="scope">
                            <el-input v-model="scope.row.src" placeholder="请输入视频链接"></el-input>
                          </template>
                        </el-table-column>
                        <el-table-column label="操作">
                          <template slot-scope="scope">
                            <!-- 上移 -->
                            <el-button icon="el-icon-arrow-up" class="handle-up-btn" type="primary"
                            v-if="scopeRoot.row.videos.length !== 1 && scope.$index !== 0" @click="arrayIndexReplace(scopeRoot.row.videos, scope.row, scope.$index, 'up')"></el-button>
                            <!-- 下移 -->
                            <el-button icon="el-icon-arrow-down" class="handle-up-btn" type="primary"
                            v-if="scope.$index !== scopeRoot.row.videos.length - 1" @click="arrayIndexReplace(scopeRoot.row.videos, scope.row, scope.$index, 'down')"></el-button>
                            <!-- 删除 -->
                            <el-button icon="el-icon-close" class="handle-up-btn" type="danger" @click="spliceData(scopeRoot.row.videos, scope.$index)"></el-button>
                          </template>
                        </el-table-column>
                      </el-table>

                    </el-form>
                  </template>
                </el-table-column>
                <el-table-column label="封面图" prop="coverImg">
                  <template slot-scope="scope">
                    <img v-if="scope.row.coverImg" :src="scope.row.coverImg" alt="封面图" />
                    <span v-else>无</span>
                  </template>
                </el-table-column>
                <el-table-column label="文本" prop="text"></el-table-column>
                <el-table-column label="视频数" prop="voides" width="60px">
                  <template slot-scope="scope">
                    <span v-if="scope.row.videos.length>0">{{scope.row.videos.length}}个</span>
                    <span v-else>无</span>
                  </template>
                </el-table-column>

                <el-table-column label="操作" width="200px">
                  <template slot-scope="scope">
                    <!-- 上移 -->
                    <el-button icon="el-icon-arrow-up" class="handle-up-btn" type="primary"
                    v-if="scope.row.videos.length !== 1 && scope.$index !== 0" @click="arrayIndexReplace(articleData.content.content, scope.row, scope.$index, 'up')"></el-button>
                    <!-- 下移 -->
                    <el-button icon="el-icon-arrow-down" class="handle-up-btn" type="primary"
                    v-if="scope.$index !== scope.row.videos.length - 1" @click="arrayIndexReplace(articleData.content.content, scope.row, scope.$index, 'down')"></el-button>
                    <!-- 删除 -->
                    <el-button icon="el-icon-close" class="handle-up-btn" type="danger" @click="spliceData(articleData.content.content, scope.$index)"></el-button>
                  </template>
                </el-table-column>
              </el-table>
               <el-form-item label="正文:" style="margin-top: 30px">
                <za-editor ref="editor" id="editor_id" height="500px" width="500px"
                  :minWidth="500" :resizeType="1" urlType="absolut"
                  :content="articleData.extraContent" :loadStyleMode="false"
                  @on-content-change="extraContentChange">
                </za-editor>
              </el-form-item>
            </div>
          </el-col>
        </el-row>

        <el-row :gutter="23">
          <el-col :span="23">
            <el-form-item label="功能组件:">
              <el-input v-model="articleData.componentsIds" placeholder="多个组件逗号隔开"></el-input>
            </el-form-item>
          </el-col>
        </el-row>

        <el-row :gutter="20">
          <el-col :span="11">
            <el-form-item label="是否外链产品:">
              <el-radio v-model="articleData.isLinkProduct" label="N">否</el-radio>
              <el-radio v-model="articleData.isLinkProduct" label="Y">是</el-radio>
            </el-form-item>
          </el-col>
          <el-col :span="12" v-if="articleData.isLinkProduct === 'Y'" style="text-align: right">
            <el-button type="success" @click="addProduction">增加产品</el-button>
          </el-col>
        </el-row>

        <!-- 外链产品 table-->
        <el-row :gutter="20" v-if="articleData.isLinkProduct === 'Y'">
          <el-table ref="productionTable" style="width: 100%" :data="articleData.productList" border>
            <el-table-column label="产品图片" width="180">
              <template slot-scope="scope">
                <el-upload class="link-production" :action="ajaxUrl.baseUrl + ajaxUrl.uploadArticleImage"
                  :show-file-list="false" :on-success="(res, file)=>{return handleProductionSuccess(res, file, scope.row)}">
                  <img v-if="scope.row.imageUrl" :src="scope.row.imageUrl" class="link-production-img">
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                  <div slot="tip" class="upload-tip">建议宽高比690:290</div>
                </el-upload>
              </template>
            </el-table-column>
            <el-table-column label="超链接">
              <template slot-scope="scope">
                <el-input v-model="scope.row.url" placeholder="请输入超链接"></el-input>
              </template>
            </el-table-column>
            <el-table-column label="操作">
              <template slot-scope="scope">
                <!-- 上移 -->
                <el-button icon="el-icon-arrow-up" class="handle-up-btn" type="primary"
                 v-if="articleData.productList.length !== 1 && scope.$index !== 0" @click="arrayIndexReplace(articleData.productList, scope.row, scope.$index, 'up')"></el-button>
                <!-- 下移 -->
                <el-button icon="el-icon-arrow-down" class="handle-up-btn" type="primary"
                 v-if="scope.$index !== articleData.productList.length - 1" @click="arrayIndexReplace(articleData.productList, scope.row, scope.$index, 'down')"></el-button>
                <!-- 删除 -->
                <el-button icon="el-icon-close" class="handle-up-btn" type="danger" @click="spliceData(articleData.productList, scope.$index)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-row>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button id="saveNormal" type="primary" @click="saveArticle($event)">保 存</el-button>
        <el-button id="saveToOnlone" type="success" @click="saveArticle($event)">上 线</el-button>
        <el-button @click="dialogFormVisible=false">取 消</el-button>
      </div>
    </el-dialog>
    <el-dialog :visible.sync="dialogVisible">
      <img width="100%" :src="dialogImageUrl" alt="">
    </el-dialog>
  </div>
</template>

<script>
import { ajaxPostCommon, ajaxUploadCommon } from "@/api/common";
import * as ajaxUrl from "@/api/url";
import { debounceFunc } from '@/utils/'
import { mapState } from "vuex";
import moment from 'moment'

const dialogTitle = {
  add: "添加资讯",
  edit: "编辑资讯"
};
export default {
  data() {
    return {
      ajaxUrl,
      uploadServer: ajaxUrl.uploadArticleImage,
      articleData: {},
      dialogFormVisible: false,
      dialogTitle: "",
      content: "",
      columnListData: [],
      articleLabels: [],
      dialogImageUrl: "",
      dialogVisible: false,
      loading: false,
      submitImageUrl: "",
      templateType: [
        { value: "v1", label: "外部爬取内容" },
        { value: "v2", label: "众安原创内容" }
      ],
      formValidate: {
        title: [
          { required: true, message: "请输入标题", trigger: "blur" },
          { max: 40, message: "最多40个字", trigger: "blur" }
        ],
        directoryId: [
          { required: true, message: "请选择所属栏目", trigger: "blur" }
        ],
        infoAccountId: [
          {required: true, message: "请选择来源", trigger: "blur"}
        ],
        labels: [
          {
            required: true,
            message: "请选择标签",
            trigger: "blur",
            type: "array"
          }
        ]
      }
    };
  },
  methods: {
    debounceFunc,
    // 初始显示dialog
    show(type, articleData) {
      // 处理Labels数据
      articleData.labels = articleData.labels
        ? this.parseLabelData(articleData.labels)
        : [];

      if (this.columnList[0].directoryName === "全部") {
        this.columnList.shift();
      }
      this.columnListData = this.columnList;

      // 判断新增or编辑
      if (type === "add") {
        this.dialogTitle = dialogTitle.add;
        this.articleData = { ...articleData, imageList: [], productList: [], isTimeliness: 'N', isLinkProduct: 'N' };
      } else {
        this.dialogTitle = dialogTitle.edit;
        this.articleData = articleData;
        this.articleData.productList = this.articleData.productList || [];
        this.articleData.imageList = this.articleData.imageList || [];
        this.articleData.validityDate = moment(this.articleData.validityDate).format('YYYY/MM/DD HH:mm:ss')
        // 判断文章类型 文字or视频
        this.parseArticleType(articleData.articleType);
      }

      this.dialogFormVisible = true;
    },

    // 隐藏对话框
    hide() {
      this.articleData.imageList = [];
      this.$refs.form.resetFields();
      this.submitImageUrl = "";
    },

    formatPublishTime(time) {
      this.articleData.publishTime = time.getTime();
    },

    validityForm() {
      let validatePass = false;
      this.$refs.form.validate(pass => {
        if (pass) validatePass = true;
      });
      return validatePass;
    },

    ctrlSubmitData() {
      let submitData = { ...this.articleData };
      // 处理labels 逗号分隔
      submitData.labels = this.handleLabelData(submitData.labels);

      if (!this.submitImageUrl) {
        submitData.coverImage = this.parseCoverImage(submitData.imageList);
      } else {
        submitData.coverImage = this.submitImageUrl;
      }
      delete submitData.imageList;
      delete submitData.columnData;

      const productList = submitData.productList.filter(item => !!item.imageUrl && !!item.url)
      submitData.productList = JSON.stringify(productList);
      return submitData;
    },

    saveArticle(e) {
      if (!this.validityForm()) return false;
      const submitData = this.ctrlSubmitData();
      // 新增
      this.loading = true;
      if (this.dialogTitle === dialogTitle.add) {
        submitData.status = "04";
        ajaxPostCommon(ajaxUrl.saveInfoArticle, submitData).then(res => {
          this.loading = false;
          this.dialogFormVisible = false;
          this.$emit("submit-article");
        });
      // 编辑
      } else {
        submitData.status = e.currentTarget.id === "saveNormal" ? submitData.status : "04";
        if (submitData.articleType === '01') {
          // 视频文章需要字符串化
          submitData.content = JSON.stringify({...submitData.content, extraContent: this.articleData.extraContent});
        }
        ajaxPostCommon(ajaxUrl.updateInfoArticle, submitData).then(res => {
          this.loading = false;
          this.dialogFormVisible = false;
          this.$emit("submit-article");
        });
      }
    },

    onContentChange(val) {
      this.articleData.content = val;
    },

    extraContentChange(val) {
      this.articleData.extraContent = val;
    },

    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },

    handleRemove(file, fileList) {
      let arr = [];
      this.articleData.imageList.find((v, i, arr) => {
        if (file.uid === v.uid) {
          arr.splice(i, 1);
        }
      });
      fileList.forEach(v => {
        arr.push(v.url);
      });
      this.submitImageUrl = arr.join(",");
    },

    changeImage(file, fileList) {},

    handleCoverSuccess(res, file) {
      let arr = [];
      this.articleData.imageList.push({ url: res.result });
      this.articleData.imageList.forEach(v => {
        arr.push(v.url);
      });
      this.submitImageUrl = arr.join(",");
    },

    beforeCoverUpload(file) {},

    parseLabelData(labelStr) {
      let tempArr = labelStr.split(",");
      return tempArr.map(i => {
        return i;
      });
    },

    parseArticleType(articleType) {
      if (articleType === '01') {
        const content = JSON.parse(this.articleData.content);
        this.articleData.content = {title: content.title, content: content.content};
        this.articleData.extraContent = content.extraContent;
      }
    },

    handleLabelData(labels) {
      let labelArray = [];
      labels.forEach(i => {
        labelArray.push(i);
      });
      return labelArray.join(",");
    },

    parseCoverImage(imageList) {
      let arr = [];
      imageList.forEach(v => {
        arr.push(v.url);
      });
      return arr.join(",");
    },

    getLabels() {
      ajaxPostCommon(ajaxUrl.getArticleLabel, {articleId: this.articleData.articleId}).then( res => {
        res.result.forEach( i => {
          if (this.articleData.labels.indexOf(i) === -1) {
            this.articleData.labels.push(i);
          }
        })
      })
    },

    addProduction() {
      this.articleData.productList.push({
        imageUrl: '',
        url: ''
      });
    },

    // 数组交换位置
    arrayIndexReplace(array, row, index, type) {
      let tempData;
      if (type === 'up') {
        tempData = array[index - 1];
        array[index - 1] = row;
      } else {
        tempData = array[index + 1];
        array[index + 1] = row;
      }
      array[index] = tempData;
      this.$set(array, index, array[index]);
    },

    // 删除数据
    spliceData(row, index) {
      row.splice(index, 1);
    },

    handleProductionSuccess(res, file, row) {
      row.imageUrl = res.result;
    },

    // 查询标签
    queryLabels(labelName) {
      if (!!labelName) {
        ajaxPostCommon(ajaxUrl.getLabels, {labelName}).then(res => {
          this.articleLabels = res.result;
        });
      } else {
        this.articleLabels = [];
      }
    }

  },

  computed: {
    ...mapState([ "columnList", "infoAccount"])
  }
};
</script>

<style lang="scss">
.title-tip {
  position: absolute;
  top: 30px;
  left: 0;
  font-size: 12px;
  color: red;
}
.link-production .avatar-uploader-icon {
  width: 140px;
  height: 60px;
  border: 1px dashed #ccc;
  line-height: 60px;
  margin-top: 20px;
}
.link-production-img {
  width: 140px;
  height: 60px;
  margin-top: 20px;
}
.upload-tip {
  color: red;
  font-size: 12px;
}

.handle-up-btn {
  border-radius: 50%;
  padding: 12px;
}

.get-label-btn {
  width: 100%;
  padding-left: 10px;
  padding-right: 10px;
}

// editor style
.edit-article-dialog {
  .dialog-wrap {
    overflow: hidden;
    overflow-y: scroll;
  }
  .el-dialog__body {
    padding-bottom: 0 !important;
  }
  .el-date-editor {
    width: 100%;
  }
  .el-date-editor.el-input {
    width: 100% !important;
  }
  .ke-icon-uploadImage {
    width: 16px;
    height: 16px;
    background-position: 0 -496px;
  }
  .ke-icon-clearStyle {
    width: 16px;
    height: 16px;
    background-position: 0 -1008px;
  }
  .ke-icon-uploadVideo {
    width: 16px;
    height: 16px;
    background-position: 0 -528px;
  }
}
</style>



