<template>
	<za-page-content :header="contentHeader" scroll="true">
		<za-table ref="table" :data="articles" :total="pageConfig.total" @selection-change="handleSelectionChange" @action-change="changePage" :isLoading="loadingTable">
			<div style="padding-bottom: 0px;" slot="searchBar">
				<el-form :model="filters" ref="filtersForm">
					<el-form-item>
						<el-col :span="8">
							<el-col :span="6">来源:</el-col>
							<el-col :span="16">
								<el-select style="width:100%" v-model="filters.infoAccountId" placeholder="请选择">
									<el-option v-for="item in origins" :key="item.infoAccountId" :label="item.nickName" :value="item.infoAccountId">
									</el-option>
								</el-select>
							</el-col>
						</el-col>
						<el-col :span="8">
							<el-col :span="6" :offset="2">状态:</el-col>
							<el-col :span="16">
								<el-select style="width:100%" v-model="filters.status" placeholder="请选择">
									<el-option v-for="item in articleStatus" :key="item.value" :label="item.label" :value="item.value">
									</el-option>
								</el-select>
							</el-col>
						</el-col>
						<el-col :span="6" :offset="2">
							<el-checkbox v-model="checkboxVal" @change="changeRecommend">运营推荐</el-checkbox>
						</el-col>
					</el-form-item>
					<el-form-item>
						<el-col :span="8">
							<el-col :span="6">文章ID:</el-col>
							<el-col :span="16">
								<el-input v-model="filters.articleId" @keyup.native.enter="filter" placeholder="文章ID"></el-input>
							</el-col>
						</el-col>

						<el-col :span="8">
							<el-col :span="6" :offset="2">标题:</el-col>
							<el-col :span="16">
								<el-input v-model="filters.title" @keyup.native.enter="filter" placeholder="搜索标题..."></el-input>
							</el-col>
						</el-col>
					</el-form-item>
					<el-form-item>
						<el-col :span="16">
							<el-col :span="3">发布时间:</el-col>
							<el-date-picker v-model="filters.timeRange" type="datetimerange" value-format="yyyy-MM-dd HH:mm:ss" start-placeholder="开始时间" end-placeholder="结束时间">
							</el-date-picker>
						</el-col>
						<el-col :span="6" align="center">
							<el-button icon="el-icon-search" type="primary" @click="filter">查询</el-button>
							<el-button icon="el-icon-refresh" type="primary" @click="resetFilter">重置</el-button>
						</el-col>
					</el-form-item>
					<el-form-item>
						<el-col :span='24'>
							<el-button type="success" @click="addArticle">添加</el-button>
							<el-button type="primary" @click="batchOnlineOrOffLine('online')">批量上线</el-button>
							<el-button type="primary" @click="batchOnlineOrOffLine('offline')">批量下线</el-button>
							<el-button type="danger" @click="batchDelete">批量删除</el-button>
						</el-col>
					</el-form-item>
				</el-form>
			</div>

			<el-table-column type="selection" width="50"></el-table-column>
			<el-table-column prop="status" :formatter="formatterStatus" label="状态" width="80"></el-table-column>
			<el-table-column prop="articleId" label="文章ID" width="160"></el-table-column>
			<el-table-column prop="title" label="标题" width="250"></el-table-column>
			<el-table-column prop="nickName" :formatter="formatterOrigin" label="来源"></el-table-column>
			<el-table-column prop="directoryName" label="所属栏目"></el-table-column>
			<el-table-column prop="weight" :formatter="formatterWeight" label="权重"></el-table-column>
			<el-table-column prop="crawlerTime" width="160" :formatter="formatterDate" label="爬取时间"></el-table-column>
			<el-table-column prop="gmtCreated" width="160" :formatter="formatterDate" label="发布时间"></el-table-column>
			<el-table-column prop="gmtModified" width="160" :formatter="formatterDate" label="修改时间"></el-table-column>
			<el-table-column label="操作" fixed="right" width="140">
				<template slot-scope="scope">
					<el-button size="mini" type="primary" @click="previewArticle(scope.row.articleId)">预览</el-button>
					<el-button size="mini" type="primary" @click="handleEdit(scope.$index, scope.row)">编辑</el-button>
					<el-button size="mini" type="primary" @click="handleWeightValue(scope.$index, scope.row)">权重</el-button>
					<el-button v-if="scope.row.status!=='04'" size="mini" type="success" @click="batchOnlineOrOffLine('online', scope.$index, scope.row)">上线</el-button>
					<el-button v-if="scope.row.status==='04'" size="mini" type="danger" @click="batchOnlineOrOffLine('offline', scope.$index, scope.row)">下线</el-button>
				</template>
			</el-table-column>
		</za-table>
		<!-- 编辑 -->
		<edit-article-dialog ref="articleDialog" @submit-article="initData"></edit-article-dialog>
		<!-- 预览 -->
		<el-dialog :visible.sync="previewArticleData.isShow" width="30%">
			<img width="100%" :src="previewArticleData.qrBase64" alt="">
		</el-dialog>
		<!-- 权重 -->
		<el-dialog :visible.sync="weightedValue.isShow" width="500px" title="修改权重">
			<el-row>
				<el-form ref="form" label-position="left" label-width="90px">
					<el-row :gutter="20">
						<el-col :span="8">
							<el-form-item label="设为热门：" prop="labelName" style="width: 100%">
								<el-checkbox v-model="weightedValue.isTop"></el-checkbox>
							</el-form-item>
						</el-col>
						<el-col :span="13" v-if="weightedValue.isTop">
							<el-form-item label="热门排序：" prop="heatSort" style="width: 100%">
								<el-input v-model="weightedValue.heatSort" type="number" placeholder="请输入0-100"></el-input>
							</el-form-item>
						</el-col>
					</el-row>

					<el-row type="flex" align="middle" v-if="weightedValue.isTop">
						<el-form-item label="热门大图" prop="heatCoverImage" style="width: 100%">
							<el-col :span="20">
								<div class="img-tip" style="color: red">* 建议宽高比690：330</div>
								<el-upload class="avatar-uploader" :action="ajaxUrl.baseUrl + ajaxUrl.uploadArticleImage" :show-file-list="false" :on-success="handleAvatarSuccess">
									<img v-if="weightedValue.heatCoverImage" :src="weightedValue.heatCoverImage" class="avatar">
									<i v-else class="el-icon-plus avatar-uploader-icon"></i>
								</el-upload>
							</el-col>
						</el-form-item>
					</el-row>

					<el-form-item v-if="!weightedValue.isTop" label="权重：" prop="labelName" style="width: 100%">
						<el-row>
							<el-col :span="16">
								<el-input v-model="weightedValue.weight" type="number" placeholder="请输入0-100" @keyup.native.enter="modifyWeightValue"></el-input>
							</el-col>
						</el-row>
					</el-form-item>
					<el-col :span="24" align="center">
						<el-button type="primary" @click="modifyWeightValue">确 定</el-button>
					</el-col>
				</el-form>
			</el-row>
		</el-dialog>
	</za-page-content>
</template>

<script>
import formatterDate from "../../filters/date/index.js";
import EditArticleDialog from "./components/EditArticleDialog.vue";
import { ajaxPostCommon } from "../../api/common.js";
import * as ajaxUrl from "../../api/url";
import { articleStatus } from "./components/staticData.js";
import { mapMutations } from "vuex";

export default {
  components: {
    EditArticleDialog
  },
  data() {
    return {
      articleStatus,
      ajaxUrl,
      id: "",
      contentHeader: "栏目",
      selectionRowData: [],
      articles: [],
      origins: [],
      isPreviewArticle: false,
      previewArticleData: {
        isShow: false,
        qrBase64: ""
      },
      weightedValue: {
        id: "",
        isTop: 0,
        isShow: false,
        weight: 0,
        heatSort: "",
        heatCoverImage: ""
      },
      filters: {
        title: "",
        status: "",
        articleId: "",
        isRecommend: "",
        publishTimeStart: "",
        publishTimeEnd: "",
        infoAccountId: "",
        timeRange: []
      },
      checkboxVal: "",
      isHotRecommend: "",
      loadingTable: false,
      pageConfig: {
        pageNum: 1,
        pageSize: 10,
        total: 1
      }
    };
  },
  created() {
		this.id = this.$route.params.id;
    this.initData();
    this.getColunmList();
    this.getListOrigin();
  },
  methods: {
    ...mapMutations(["initInfoAccount", "initColumnList"]),

    filter() {
      const publishTimeStart = !!this.filters.timeRange
        ? this.filters.timeRange[0]
        : "";
      const publishTimeEnd = !!this.filters.timeRange
        ? this.filters.timeRange[1]
        : "";
      this.pageConfig = {
        ...this.pageConfig,
        ...this.filters,
        publishTimeStart,
        publishTimeEnd,
        pageNum: 1
      };
      delete this.pageConfig.timeRange;
      if (!this.$refs.table.resetCurrentPage()) {
        this.initData();
      }
    },

    resetFilter() {
      this.filters = {};
      this.checkboxVal = false;
      this.pageConfig = {
        pageSize: 10,
        pageNum: 1
      };
      this.initData();
    },

    initData() {
      this.loadingTable = true;
      ajaxPostCommon(ajaxUrl.getArticleList, {
        directoryId: this.id,
        ...this.pageConfig
      }).then(res => {
        // 被迫异步(未知问题)
        setTimeout(() => {
          this.contentHeader = `栏目-${res.result.directoryName}`;
          this.articles = res.result.list;
          this.pageConfig.total = res.result.total;
          this.loadingTable = false;
        }, 200);
      });
    },

    getColunmList() {
      ajaxPostCommon(ajaxUrl.getListInfoDirectory).then(res => {
        res.result.unshift({ directoryName: "全部" });
        this.initColumnList(res.result);
      });
    },

    getListOrigin() {
      ajaxPostCommon(ajaxUrl.getListAccount).then(res => {
        this.origins = res.result;
        this.initInfoAccount({ ...this.origins });
        this.origins.unshift({ infoAccountId: "", nickName: "全部" });
      });
    },

    addArticle() {
      this.$refs.articleDialog.show("add", {});
    },
    // 文章上下线 文章状态: 01-草稿 04-上线 05-下线
    batchOnlineOrOffLine(type, index, row) {
      let tripMsg = "",
        status = "",
        articleArray = [],
        articleIds = "";

      if (type === "online") {
        status = "04";
        tripMsg = "选中文章上线?";
      } else {
        status = "05";
        tripMsg = "选中文章下线?";
      }
      if (!!row) {
        articleIds = row.articleId;
      } else {
        this.selectionRowData.forEach(i => {
          articleArray.push(i.articleId);
        });
        articleIds = articleArray.join(",");

        if (!articleIds) {
          this.$message({
            showClose: true,
            message: "请选择文章",
            type: "error"
          });
          return false;
        }
      }

      this.$confirm(tripMsg, "提示", {})
        .then(() => {
          ajaxPostCommon(
            ajaxUrl.batchLineOrDownLine,
            { list: articleIds, status: status },
            "application/json; charset=utf-8"
          ).then(res => {
            if (res.returnCode == 200) {
              let successMsg = status === "04" ? "上线成功" : "下线成功";
              this.$message({
                showClose: true,
                message: successMsg,
                type: "success"
              });
              this.initData();
            } else {
              this.$message({
                showClose: true,
                message: res.returnMsg,
                type: "error"
              });
            }
          });
        })
        .catch(() => {});
    },

    // 删除文章
    batchDelete(i, r) {
      let tripMsg = "",
        articleArray = [],
        articleIds = "";
      if (!r) {
        tripMsg = "删除选中文章么?";
        this.selectionRowData.forEach(i => {
          articleArray.push(i.articleId);
        });
        articleIds = articleArray.join(",");
      } else {
        tripMsg = "删除该条文章么?";
        articleIds = r.articleId;
      }

      if (!articleIds) {
        this.$message({
          showClose: true,
          message: "请选择文章",
          type: "error"
        });
        return false;
      }
      this.$confirm(tripMsg, "提示", {})
        .then(() => {
          ajaxPostCommon(ajaxUrl.deleteInfoArticle, { list: articleIds })
            .then(res => {
              if (res.returnCode == 200) {
                this.$message({
                  showClose: true,
                  message: "删除成功",
                  type: "success"
                });
                this.initData();
              } else {
                this.$message({
                  showClose: true,
                  message: res.returnMsg,
                  type: "error"
                });
              }
            })
            .catch(err => {});
        })
        .catch(() => {});
    },

    formatterStatus(r, c, v) {
      switch (v) {
        case "01":
          return "草稿";
          break;
        case "04":
          return "上线";
          break;
        case "05":
          return "下线";
          break;
        default:
          return "下线";
      }
    },

    formatterOrigin(r, c, v) {
      return v ? v : "未分类";
    },

    formatterDate(r, c, v) {
      return formatterDate(v) || "无";
    },

    formatterWeight(r, c, v) {
      return !!r.isTop ? "热门" : r.weight;
    },

    handleEdit(index, rowData) {
      ajaxPostCommon(ajaxUrl.getArticleDetail, {
        articleId: rowData.articleId
      }).then(res => {
        this.$refs.articleDialog.show("edit", { ...res.result });
      });
    },

    handleWeightValue(i, r) {
      this.weightedValue = {
        id: r.id,
        isShow: true,
        isTop: r.isTop === 1 ? true : false,
        weight: r.weight,
        heatSort: r.heatSort,
        heatCoverImage: r.heatCoverImage
      };
    },

		validateWeightValue() {
			let errorMsg = "";
			if (this.weightedValue.isTop) {
				const { heatSort, heatCoverImage } = this.weightedValue;
				if (Number(heatSort) < 0) {
					errorMsg = "热门排序不能小于0";
				} else if (!heatCoverImage) {
					errorMsg = "请上传热门大图!";
				}
      } else {
				let weightValue = Number(this.weightedValue.weight);
        if (weightValue !== 0 && (!weightValue || weightValue < 0 || weightValue > 100)) {
          errorMsg = "请输入0-100的权重值";
        }
      }

      if (!!errorMsg) {
        this.$message({
          showClose: true,
          message: errorMsg,
          type: "error"
				});
				return false;
			}
			return true;
		},

    // 修改权重
    modifyWeightValue() {
      let isTop = !!this.weightedValue.isTop ? "1" : "0",
        submitData = { ...this.weightedValue, isTop };

			if (!this.validateWeightValue()) {
				return false;
			}

      ajaxPostCommon(ajaxUrl.modifyWeight, submitData).then(res => {
        if (res.returnCode == 200) {
          this.$message({
            showClose: true,
            message: "修改成功",
            type: "success"
          });
          this.initData();
          this.weightedValue.isShow = false;
        } else {
          this.$message({
            showClose: true,
            message: res.returnMsg,
            type: "error"
          });
        }
      });
    },

    handleSelectionChange(selection) {
      this.selectionRowData = selection;
    },

    changePage(params) {
      let pageParams = {
        pageNum: params.page,
        pageSize: params.rows || this.pageConfig.pageSize
      };
      this.pageConfig = { ...this.pageConfig, ...pageParams };
      this.initData();
    },

    changeRecommend(v) {
      this.filters.isRecommend = !!v ? "Y" : "";
    },

    handleAvatarSuccess(res) {
      this.weightedValue.heatCoverImage = res.result;
    },

    // 预览, 显示二维码
    previewArticle(articleId) {
      ajaxPostCommon(ajaxUrl.previewArticle, { articleId }).then(res => {
        if (res.returnCode == 200) {
          this.previewArticleData.qrBase64 = `data:image/png;base64,${
            res.result
          }`;
          this.previewArticleData.isShow = true;
        } else {
          this.$message({
            showClose: true,
            message: res.returnMsg,
            type: "error"
          });
        }
      });
    }
  },

  // 路由变化 复用组件
  beforeRouteUpdate(to, from, next) {
    this.id = to.params.id;
    this.initData();
    next();
  }
};
</script>

<style lang="scss" scoped>
.el-date-editor.el-input {
  width: auto;
}
.el-button + .el-button {
  margin-left: 0;
  margin-top: 2px;
}
</style>



